.second-list {
  display: none;
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 15px;
  list-style-type: none;
  max-width: 850px;
}
.second-list__link {
  display: block;
  box-sizing: border-box;
  padding: 21px 24px 21px 35px;
  color: #000000;
  text-decoration: none;
  margin: 0 auto;
}
@media (min-width: @desktop-width) {
  .second-list {
    display: flex;
    flex-wrap: wrap;
    order: 4;
  }
}
